import * as g2 from "@antv/g2"

{
    const f = (n: number) => Math.pow(0.9, n)

    const chart = new g2.Chart({
        container: document.getElementById("q1"),
        autoFit: false,
        width: 600,
        height: 300,
    });

    chart
        .data(
            [
                ...new Array(21).fill(0).map((_, idx) => ({ n: idx, value: f(idx) }))
            ]
        )
        .line()
        .position('n*value')
    chart.render()
}

{
    // impulse function
    const x = (n: number) => n == 0 ? 1 : 0

    const h = (x: (n: number) => number) => {
        //  cache 
        const cache: number[] = []
        const f = (n: number) => {
            //  n < 0  0
            if (n < 0) return 0
            //  cache cache 
            else if (cache[n] != undefined) return cache[n]
            //  cache cache 
            else return cache[n] =
                //  f(n - 1) 
                1.8 * Math.cos(Math.PI / 16) * f(n - 1) -
                //  f(n - 2) 
                0.81 * f(n - 2) +
                x(n) +
                (1 / 2) * x(n - 1)
        }
        return f
    }

    // 
    const f = h(x)

    const chart = new g2.Chart({
        container: document.getElementById("q2a"),
        autoFit: false,
        width: 600,
        height: 300,
    });

    chart
        .data(
            [
                ...new Array(10).fill(0).map((_, idx) => ({ n: -(idx + 1), value: 0 })).reverse(),
                ...new Array(100).fill(0).map((_, idx) => ({ n: idx, value: f(idx) }))
            ]
        )
        .line()
        .position('n*value')
    chart.render()
}

{
    // impulse function
    const x = (n: number) => n == 0 ? 1 : 0

    /**
     * 
     * @param b b(i) = bi
     * @param a a(j) = aj and a(0) = 1
     * @param x 
     * 
     */
    const filter = (
        b: (n: number) => number,
        a: (n: number) => number,
        x: (n: number) => number) => {
        //  a(0) != 1 
        if (a(0) != 1) {
            console.error("a[0] must equal 1")
            return
        }
        //  cache 
        const cache: number[] = []

        /**
         * 
         * @param n 
         * 
         * @return  b0x[n] + b1x[n-1] + ... + bnx[0]
         *          - a1y[n-1] - a2y[n-2] - ... - any[0]
         */
        const f = (n: number) => {
            //  n < 0  0
            if (n < 0) return 0
            //  cache cache 
            else if (cache[n] != undefined) return cache[n]
            else {
                //  matlab  filter 
                let out = 0
                for (let k = 0; k <= n; k++) {
                    out += x(n - k) * b(k) -
                        f(n - (k + 1)) * a(k + 1)
                }
                cache[n] = out
                return out
            }
        }
        return f
    }

    //  array  function
    const arr2fn = (arr: number[]) => {
        return (n: number) => {
            if (arr[n] != undefined) return arr[n]
            else return 0
        }
    }

    // 
    let f = filter(
        arr2fn([1, 1 / 2]),
        arr2fn([1, -1.8 * Math.cos(Math.PI / 16), 0.81]),
        x)

    const chart = new g2.Chart({
        container: document.getElementById("q2b"),
        autoFit: false,
        width: 600,
        height: 300,
    });

    chart
        .data(
            [
                ...new Array(10).fill(0).map((_, idx) => ({ n: -(idx + 1), value: 0 })).reverse(),
                ...new Array(101).fill(0).map((_, idx) => ({ n: idx, value: f(idx) }))
            ]
        )
        .line()
        .position('n*value')
    chart.render()
}